@import '../variable';
@import "../../../node_modules/bootstrap/scss/mixins";
@import '../layout/layout';

#header{
    height: px-vw(100);
    min-height: 70px;
    display: flex;
    justify-content: space-between;
    z-index: 10000;
    position: fixed;
    width: 100vw;
    background-color: white;
    padding: 0 px-vw(50) 0 px-vw(50);
    vertical-align: middle;
    box-shadow: 0px 3px 6px rgba(0,0,0, .1)
}

.logo-wrapper{
    height: 100%;
    max-height: 75px;
    margin: auto 0;
    img{
        height: 100%;
    }
}

.pc-nav{
    display: flex;
    justify-content: end;
    .nav-list{
        display: inline-flex;
        margin: auto 0;
        list-style: none;
    }
    
    .item-link{
        text-decoration: none;
        font-size: px-vw(25);
        color: $blue;
        font-family:Noto Sans JP;
        margin-right: px-vw(35);
    }

    .nav-btn{
        display: inline-flex;
        list-style: none;
        margin: auto 0;
        li{
            width: px-vw(250);
            height: px-vw(60);
            border-radius: px-vw(30);
            text-align: center;
            padding-top: px-vw(5);
            &.white-outline-btn{
                border: px-vw(3) solid $blue;
                margin-right: px-vw(10);
                a{
                    @extend .item-link;
                    margin: auto 0;
                }
                &:hover{
                    background-color: #c3ebfa;
                }
            }
            &.blue-fill-btn{
                background-color: $blue;
                color: white;
                a{
                    @extend .item-link;
                    color: white;
                    margin: 0;
                }
                &:hover{
                    background-color: #2caddb;
                }
            }
        }
    }
}


.sp-nav{
    display: none;
    .nav-mail{
        width: 70px;
        height: 70px;
        text-align: center;
        background-color: #F1F2F3;
        .mail-wrap{
            margin-top: 12px;
        }
        .toggler-name{
            font-size: 12px;
            margin-top: 5px;
            color: $blue;
        }
        &:hover{
            background-color: #e5e8eb;
        }
    }
    .nav-toggler {
        position: relative;
        z-index: 1;
        border: 0;
        background: #007AA8;
        border-radius: 0;
        height: 100%;
        width: 70px;
        cursor: pointer;      
        .icon-wrap{
            width: 45%;
            margin: 18px auto 0;
            .icon-bar {
                display: block;
                width: 100%;
                height: 3px;
                background: #fff;
                transition: .3s;
                &+.icon-bar{
                    margin-top: 5px;
                }
            }
        }
        .toggler-name{
            font-size: 12px;
            text-align: center;
            margin-top: 5px;
            color: #fff;
        }
    }
}




.nav-toggler:focus-within .nav-toggler {
    pointer-events: none;
}

.nav-toggler:focus-within .icon-bar:nth-of-type(1) {
    transform: translate3d(0, 8px, 0) rotate(45deg);
}

.nav-toggler:focus-within .icon-bar:nth-of-type(2) {
    opacity: 0;
}

.nav-toggler:focus-within .icon-bar:nth-of-type(3) {
    transform: translate3d(0, -8px, 0) rotate(-45deg);
}



@include media-breakpoint-down(md){
    #header{
        height: 70px;
        padding: 0;
        .logo-wrapper{
            max-width: 200px;
            width: 100%;
        }
        .sp-nav{
            display: inline-flex;
        }
        .pc-nav{
            display: none;
        }
    }
}

.container{
    padding-left: px-vw(200);
    padding-right: px-vw(200);
    max-width: 100%;
    margin: 0;
    @include media-breakpoint-down(sm){
        padding-left: 20px;
        padding-right: 20px;
    }
}


#top{
    min-height: 100vh;
    padding-top: px-vw(100);
    padding-bottom: 62px;
    background-color: $blue;
    .sns{
        display: flex;
        justify-content: space-between;
        .sns-description{
            width: px-vw(850);
            
            .sub-title{
                color: $blue;
                max-width: 570px;
                background-color: white;
            }
            
        }
        .img-wrap{
            width: px-vw(670);
            float: right;
            display: flex;
            align-items: center;
            img{
                max-height: 600px;
                object-fit: contain; 
                width: 100%; 
            }
            &.sp{display: none;}
        }
    }

    .download-btn-wrap{
        margin-top: 40px;
        @include media-breakpoint-down(sm){
            margin-top: 29px;
        }
    }
    
    @include media-breakpoint-down(lg){
        display: block;
        text-align: center;
        padding-top: 70px;
        .sns{
            .sns-description{
                width: 100%;
                .sub-title{
                    max-width: 100%;
                    margin: 0px 30px;
                }
            }
            .img-wrap{
                width: 100%;
                &.sp{display: block;}
                &.pc{display: none;}
            }
        }
    }
    @include media-breakpoint-down(sm){
        min-height: initial;
        padding-bottom: 29px;
        .img-wrap{
            height: 250px;
            margin-top: -20px;
            img{
                height: 100%;
                object-fit: contain;
            }
        }
    }
}



#issue{
    padding-top: 150px;
    padding-bottom: 200px;
    background-color: #E9F3FF;
    @include media-breakpoint-down(sm){
        padding-top: 33px;
        padding-bottom: 39px;
    }
    .quiz{
        display: flex;
        margin: 60px 60px 0 60px;
        &-item{
            width: 33.33%;
            margin-right: px-vw(70);
            &:last-child{
                margin-right: 0px;
            }
            .quiz-mark{
                padding:30px 10px;
                border: 3px solid $blue;
                border-radius: 15px;
                font-weight: bold;
                text-align: center;
                position: relative;
                background-color: white;
                &::before{
                    border-color: #00A0E9 transparent transparent transparent;
                    border-style: solid;
                    border-width: 25px;
                    content: "";    
                    left: 50%;
                    margin-left: -15px;
                    position: absolute;
                    top: 100%;
                }
                &:after{
                    border-color: #ffffff transparent transparent transparent;
                    border-style: solid;
                    border-width: 22px;
                    content: "";    
                    left: 50%;
                    margin-left: -12px;
                    margin-top: -1px;
                    position: absolute;
                    top: 100%;
                }
            }
            .quiz-img-wrap{
                margin-top: 85px;
                text-align: center;
            }
            .quiz-desp{
                margin-top: 60px;
                font-size: 20px;
                font-weight: bold;
                text-align: center;
            }
            .arrow-bottom{
                position: relative;
                margin-top: 60px;
                &:after{
                    border-color: #E83D1B transparent transparent transparent;
                    border-style: solid;
                    border-width: 25px;
                    content: "";    
                    left: 50%;
                    margin-left: -15px;
                    position: absolute;
                    top: 100%;
                }
            }
        }
     
    }
    .txt-solve{
        margin-top: 145px;
    }

    @include media-breakpoint-down(lg){
        .quiz{
            flex-direction: column;
            &-item{
                width: 100%;
            }
        }
    }
    @include media-breakpoint-down(sm){
        .quiz{
            margin: 25px 15px 0 15px;
            &-item{
                margin-right: 0px;
                margin-bottom: 55px;
                .quiz-mark{
                    padding: 15px 5px;
                    &::before{
                        border-width: 15px;
                    }
                    &:after{
                        border-width: 12px;
                    }
                }
                .quiz-img-wrap{
                    height: 135px;
                    margin-top: 18px;
                    img{
                        height: 100%;
                        object-fit: contain;
                    }
                }
                .quiz-desp{
                    margin-top: 13px;
                    font-size: 16px;
                }
                .arrow-bottom{
                    margin-top: 31px;
                    display: none;
                    &:after{
                        border-width: 15px;
                    }
                }
                &:last-child{
                    margin-bottom: 0px;
                    .arrow-bottom{
                        display: block;
                    }
                }
            }
        }
        .txt-solve{
            margin-top: 46px;
        }
    }
}