body {
  font-size: 16px;
  font-family: Noto Sans JP, "Hiragino Sans";
}

.download-btn {
  max-width: 656px;
  width: 100%;
  height: 113px;
  background-color: #FF7500;
  border-radius: 19px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: auto;
}

.download-btn span {
  font-size: 40px;
  font-weight: bold;
}

.download-btn .circle {
  position: absolute;
  border-radius: 50%;
  width: 83px;
  height: 83px;
  right: 23px;
  background-color: white;
  font-size: 30px;
  color: #FF7500;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 575.98px) {
  .download-btn {
    width: 247px;
    height: 48px;
    border-radius: 10px;
  }
  .download-btn span {
    font-size: 15px;
  }
  .download-btn .circle {
    width: 30px;
    height: 30px;
    font-size: 12px;
  }
}

@media screen and (max-width: 300px) {
  .download-btn {
    width: 100%;
  }
}

