#header {
  height: 5.20833vw;
  min-height: 70px;
  display: flex;
  justify-content: space-between;
  z-index: 10000;
  position: fixed;
  width: 100vw;
  background-color: white;
  padding: 0 2.60417vw 0 2.60417vw;
  vertical-align: middle;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1);
}

.logo-wrapper {
  height: 100%;
  max-height: 75px;
  margin: auto 0;
}

.logo-wrapper img {
  height: 100%;
}

.pc-nav {
  display: flex;
  justify-content: end;
}

.pc-nav .nav-list {
  display: inline-flex;
  margin: auto 0;
  list-style: none;
}

.pc-nav .item-link, .pc-nav .nav-btn li.white-outline-btn a, .pc-nav .nav-btn li.blue-fill-btn a {
  text-decoration: none;
  font-size: 1.30208vw;
  color: #0E96C9;
  font-family: Noto Sans JP;
  margin-right: 1.82292vw;
}

.pc-nav .nav-btn {
  display: inline-flex;
  list-style: none;
  margin: auto 0;
}

.pc-nav .nav-btn li {
  width: 13.02083vw;
  height: 3.125vw;
  border-radius: 1.5625vw;
  text-align: center;
  padding-top: 0.26042vw;
}

.pc-nav .nav-btn li.white-outline-btn {
  border: 0.15625vw solid #0E96C9;
  margin-right: 0.52083vw;
}

.pc-nav .nav-btn li.white-outline-btn a {
  margin: auto 0;
}

.pc-nav .nav-btn li.white-outline-btn:hover {
  background-color: #c3ebfa;
}

.pc-nav .nav-btn li.blue-fill-btn {
  background-color: #0E96C9;
  color: white;
}

.pc-nav .nav-btn li.blue-fill-btn a {
  color: white;
  margin: 0;
}

.pc-nav .nav-btn li.blue-fill-btn:hover {
  background-color: #2caddb;
}

.sp-nav {
  display: none;
}

.sp-nav .nav-mail {
  width: 70px;
  height: 70px;
  text-align: center;
  background-color: #F1F2F3;
}

.sp-nav .nav-mail .mail-wrap {
  margin-top: 12px;
}

.sp-nav .nav-mail .toggler-name {
  font-size: 12px;
  margin-top: 5px;
  color: #0E96C9;
}

.sp-nav .nav-mail:hover {
  background-color: #e5e8eb;
}

.sp-nav .nav-toggler {
  position: relative;
  z-index: 1;
  border: 0;
  background: #007AA8;
  border-radius: 0;
  height: 100%;
  width: 70px;
  cursor: pointer;
}

.sp-nav .nav-toggler .icon-wrap {
  width: 45%;
  margin: 18px auto 0;
}

.sp-nav .nav-toggler .icon-wrap .icon-bar {
  display: block;
  width: 100%;
  height: 3px;
  background: #fff;
  transition: .3s;
}

.sp-nav .nav-toggler .icon-wrap .icon-bar + .icon-bar {
  margin-top: 5px;
}

.sp-nav .nav-toggler .toggler-name {
  font-size: 12px;
  text-align: center;
  margin-top: 5px;
  color: #fff;
}

.nav-toggler:focus-within .nav-toggler {
  pointer-events: none;
}

.nav-toggler:focus-within .icon-bar:nth-of-type(1) {
  transform: translate3d(0, 8px, 0) rotate(45deg);
}

.nav-toggler:focus-within .icon-bar:nth-of-type(2) {
  opacity: 0;
}

.nav-toggler:focus-within .icon-bar:nth-of-type(3) {
  transform: translate3d(0, -8px, 0) rotate(-45deg);
}

@media (max-width: 767.98px) {
  #header {
    height: 70px;
    padding: 0;
  }
  #header .logo-wrapper {
    max-width: 200px;
    width: 100%;
  }
  #header .sp-nav {
    display: inline-flex;
  }
  #header .pc-nav {
    display: none;
  }
}

.container {
  padding-left: 10.41667vw;
  padding-right: 10.41667vw;
  max-width: 100%;
  margin: 0;
}

@media (max-width: 575.98px) {
