}

#top {
  min-height: 100vh;
  padding-top: 5.20833vw;
  padding-bottom: 62px;
  background-color: #0E96C9;
}

#top .sns {
  display: flex;
  justify-content: space-between;
}

#top .sns .sns-description {
  width: 44.27083vw;
}

#top .sns .sns-description .sub-title {
  color: #0E96C9;
  max-width: 570px;
  background-color: white;
}

#top .sns .img-wrap {
  width: 34.89583vw;
  float: right;
  display: flex;
  align-items: center;
}

#top .sns .img-wrap img {
  max-height: 600px;
  object-fit: contain;
  width: 100%;
}

#top .sns .img-wrap.sp {
  display: none;
}

#top .download-btn-wrap {
  margin-top: 40px;
}

@media (max-width: 575.98px) {
  #top .download-btn-wrap {
    margin-top: 29px;
  }
}

@media (max-width: 991.98px) {
  #top {
    display: block;
    text-align: center;
    padding-top: 70px;
  }
  #top .sns .sns-description {
    width: 100%;
  }
  #top .sns .sns-description .sub-title {
    max-width: 100%;
    margin: 0px 30px;
  }
  #top .sns .img-wrap {
    width: 100%;
  }
  #top .sns .img-wrap.sp {
    display: block;
  }
  #top .sns .img-wrap.pc {
    display: none;
  }
}

@media (max-width: 575.98px) {
  #top {
    min-height: initial;
    padding-bottom: 29px;
  }
  #top .img-wrap {
    height: 250px;
    margin-top: -20px;
  }
  #top .img-wrap img {
    height: 100%;
    object-fit: contain;
  }
}

#issue {
  padding-top: 150px;
  padding-bottom: 200px;
  background-color: #E9F3FF;
}

@media (max-width: 575.98px) {
  #issue {
    padding-top: 33px;
    padding-bottom: 39px;
  }
}

#issue .quiz {
  display: flex;
  margin: 60px 60px 0 60px;
}

#issue .quiz-item {
  width: 33.33%;
  margin-right: 3.64583vw;
}

#issue .quiz-item:last-child {
  margin-right: 0px;
}

#issue .quiz-item .quiz-mark {
  padding: 30px 10px;
  border: 3px solid #0E96C9;
  border-radius: 15px;
  font-weight: bold;
  text-align: center;
  position: relative;
  background-color: white;
}

#issue .quiz-item .quiz-mark::before {
  border-color: #00A0E9 transparent transparent transparent;
  border-style: solid;
  border-width: 25px;
  content: "";
  left: 50%;
  margin-left: -15px;
  position: absolute;
  top: 100%;
}

#issue .quiz-item .quiz-mark:after {
  border-color: #ffffff transparent transparent transparent;
  border-style: solid;
  border-width: 22px;
  content: "";
  left: 50%;
  margin-left: -12px;
  margin-top: -1px;
  position: absolute;
  top: 100%;
}

#issue .quiz-item .quiz-img-wrap {
  margin-top: 85px;
  text-align: center;
}

#issue .quiz-item .quiz-desp {
  margin-top: 60px;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}

#issue .quiz-item .arrow-bottom {
  position: relative;
  margin-top: 60px;
}

#issue .quiz-item .arrow-bottom:after {
  border-color: #E83D1B transparent transparent transparent;
  border-style: solid;
  border-width: 25px;
  content: "";
  left: 50%;
  margin-left: -15px;
  position: absolute;
  top: 100%;
}

#issue .txt-solve {
  margin-top: 145px;
}

@media (max-width: 991.98px) {
  #issue .quiz {
    flex-direction: column;
  }
  #issue .quiz-item {
    width: 100%;
  }
}

@media (max-width: 575.98px) {
  #issue .quiz {
    margin: 25px 15px 0 15px;
  }
  #issue .quiz-item {
    margin-right: 0px;
    margin-bottom: 55px;
  }
  #issue .quiz-item .quiz-mark {
    padding: 15px 5px;
  }
  #issue .quiz-item .quiz-mark::before {
    border-width: 15px;
  }
  #issue .quiz-item .quiz-mark:after {
    border-width: 12px;
  }
  #issue .quiz-item .quiz-img-wrap {
    height: 135px;
    margin-top: 18px;
  }
  #issue .quiz-item .quiz-img-wrap img {
    height: 100%;
    object-fit: contain;
  }
  #issue .quiz-item .quiz-desp {
    margin-top: 13px;
    font-size: 16px;
  }
  #issue .quiz-item .arrow-bottom {
    margin-top: 31px;
    display: none;
  }
  #issue .quiz-item .arrow-bottom:after {
    border-width: 15px;
  }
  #issue .quiz-item:last-child {
    margin-bottom: 0px;
  }
  #issue .quiz-item:last-child .arrow-bottom {
    display: block;
  }
  #issue .txt-solve {
    margin-top: 46px;
  }
}
